import { UI } from 'rcfm/helper'
import React from 'react'



export default function WhenBranchRuleLogic(props = {}) {
  const Helper = UI.Section.Member.Condition
  const SingleSelect = UI.use('React.Component.Form.SingleSelect')
  let { value, onChange } = props
  return (
    <SingleSelect className="rcfm-member-prop-editor-condition-rule-logic"
      options={Helper.getLogics()}
      value={value.logic} onChange={(logic) => onChange({
        target: 'rule', action: 'update', logic,
      })}
    />
  )
}
