import { Struct } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



export default function Renderer(properties = {}) {
  const Helper = UI.Section.Field
  const UseReactive = UI.React.hooks.useReactive
  const Field = UI.use('React.Component.Form.Field')
  let { Component, props } = properties
  let { reactive, member, onAction } = UI.Section.Member.getProps(props)
  let { values, errors } = UseReactive(reactive, { id: member.id })
  return (
    <Field
      className={`rcfm-${Struct.string.dashCase(values.type)}`}
      label={values.label}
      hint={values.hint}
      error={errors?.value}
      required={Helper.isRequired({ reactive, member })}
    >
      <Component
        name={values.name}
        value={values.value}
        invalid={errors ? true : false}
        readonly={Helper.isReadonly({ reactive, member })}
        placeholder={'placeholder' in values ? values.placeholder : null}
        options={'options' in values ? values.options : null}
        onChange={(value) => onAction && onAction({
          action: 'update', reactive,
          id: member.id, prop: 'value', value,
        })}
      />
    </Field>
  )
}
