import { UI } from 'rcfm/helper'
import React from 'react'



export default function Editor(props = {}) {
  const Helper = UI.Section.Member.Prop
  const Highlight = UI.use('React.Component.Highlight')
  const Field = UI.use('React.Component.Form.Field')
  const UseReactive = UI.React.hooks.useReactive
  const UseNonblankName = UI.React.hooks.useNonblankName
  let { reactive, member, value, exception, onChange } = props
  let PropType = Helper.getType({ reactive, member })
  const Editor = UI.use(`React.Component.Form.${PropType}`)
  let name = UseNonblankName()
  let state = UseReactive(reactive, { id: member.id, prop: 'calculation' })
  let { placeholder, hint } = reactive.schema.for(member)
  let isCalculationDefined = state && state.raw.value
  return (
    <Highlight
      type="error"
      hint={exception?.message}
      on={exception ? true : false}
    >
      <Field hint={hint} >
        <Editor
          readonly={isCalculationDefined ? true : false}
          name={name} placeholder={placeholder}
          value={value} onChange={onChange}
        />
      </Field>
    </Highlight>
  )
}
