import { UI } from 'rcfm/helper'
import React from 'react'



export default function Editor(props = {}) {
  const Helper = UI.Section.Member.Calculation
  const Highlight = UI.use('React.Component.Highlight')
  const Field = UI.use('React.Component.Form.Field')
  const ObjectEditor = UI.use('React.Component.Form.Object')
  const UseNonblankName = UI.React.hooks.useNonblankName
  let { reactive, member, value, exception, onChange } = Helper.getProps(props)
  let name = UseNonblankName()
  let { placeholder, hint } = reactive.schema.for(member)
  return (
    <Highlight
      type="error"
      hint={exception?.message}
      on={exception ? true : false}
    >
      <Field hint={hint} >
        <ObjectEditor
          multiline={true}
          name={name} placeholder={placeholder}
          value={value} onChange={onChange}
        />
      </Field>
    </Highlight>
  )
}
