import { UI } from 'rcfm/helper'
import React from 'react'



export default function Fieldset(props = {}) {
  const UseReactive = UI.React.hooks.useReactive
  let { reactive, member } = UI.Section.Member.getProps(props)
  let state = UseReactive(reactive, { id: member.id })
  let { label, description } = state.values
  if (!label && !description) { return null }
  return (<div className="rcfm-fieldset">
    {label && <h2 className="rcfm-fieldset-title">{label}</h2>}
    {description && <p className="rcfm-fieldset-description">{description}</p>}
  </div>)
}
