import { UI } from 'rcfm/helper'
import React from 'react'



export default function TextInput(props = {}) {
  const Helper = UI.Component.Form.TextInput
  const UseNonblankName = UI.React.hooks.useNonblankName
  let {
    id, multiline, name, type, value, placeholder,
    disabled, readonly, onChange, customProps,
  } = Helper.getProps(props)
  name = UseNonblankName(name)
  let className = UI.joinClass({
    root:'rcfm-component',
    klass: multiline ? 'fmtextarea' : 'fminput',
  }, Helper.joinModifierClass(props), props.className)
  return (<div id={id} className={className} >
    {
      multiline ?
        <textarea {...{
          ...customProps,
          className: 'rcfm-fmtag', name, value, placeholder,
          disabled: readonly || disabled, readOnly: readonly || disabled,
          onChange(e) { onChange && onChange(e.target.value) },
        }} /> :
        <input {...{
          ...customProps,
          className: 'rcfm-fmtag', type, name, value, placeholder,
          disabled: readonly || disabled, readOnly: readonly || disabled,
          onChange(e) { onChange && onChange(e.target.value) },
        }} />
    }
  </div>)
}
