import { Struct } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



export default function Action(props = {}) {
  const Helper = UI.Section.Member
  const UseReactive = UI.React.hooks.useReactive
  const Button = UI.use('React.Component.Button')
  let { reactive, member, onAction } = Helper.getProps(props)
  let parentId = reactive.family.ancestor({ id: member.id }).id
  let type = UseReactive(reactive, { id: member.id, prop: 'type' }).value
  let order = UseReactive(reactive, { id: member.id, prop: 'order' }).value
  let siblings = UseReactive(reactive, { id: parentId, prop: 'children' }).value
  let typeDashed = Struct.string.dashCase(type)
  return (<div className={UI.joinClass({
    root: 'rcfm-form-builder',
    klass: ['actions', `${typeDashed}-actions`],
  })}>
    <Button
      className="rcfm-form-builder-action-delete"
      circle icon="cross" color="danger" size="small"
      onClick={() => onAction && onAction({
        action: 'delete',
        reactive, id: member.id,
      })}
    />
    <Button
      className="rcfm-form-builder-action-decrease"
      circle icon="arrow-up" color="secondary" size="small"
      disabled={order === 0}
      onClick={() => onAction && onAction({
        action: 'update',
        reactive, id: member.id,
        prop: 'order', value: order - 1,
      })}
    />
    <Button
      className="rcfm-form-builder-action-increase"
      circle icon="arrow-down" color="secondary" size="small"
      disabled={order === siblings.length - 1}
      onClick={() => onAction && onAction({
        action: 'update',
        reactive, id: member.id,
        prop: 'order', value: order + 1,
      })}
    />
    <Button
      className="rcfm-form-builder-action-edit"
      circle icon="more" color="secondary" size="small"
      onClick={() => onAction && onAction({
        action: 'edit',
        reactive, id: member.id,
      })}
    />
  </div>)
}
