import { UI } from 'rcfm/helper'
import React from 'react'



export default function Alert(props = {}) {
  let { type, customProps } = UI.getProps(props, {
    type: { dataType: 'String', default: 'info' },
  })
  let className = UI.joinClass(
    { root: 'rcfm-component-alert', klass: type }, props.className,
  )
  return (
    <div {...{ ...customProps, className }} >
      {props.children}
    </div>
  )
}
