import { Struct, I18n } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



export default function Editor(props = {}) {
  const Helper = UI.Section.Member.Prop
  const UseReactive = UI.React.hooks.useReactive
  const Tabs = UI.use('React.Component.Tabs')
  const TabPane = UI.use('React.Component.TabPane')
  const Condition = UI.use('React.Section.Member.PropEditor.Condition')
  let { reactive, member, onAction } = Helper.getProps(props)
  let type = reactive.get({ id: member.id, prop: 'type' })
  let TypeCap = Struct.string.capitalize(type)
  let PropCap = Struct.string.capitalize(member.prop)
  const PropEditor = UI.try([
    `React.Section.${TypeCap}.PropEditor.${PropCap}`,
    `React.Section.Member.PropEditor.${PropCap}`,
    'React.Section.Member.PropEditor.Simple',
  ])
  let state = UseReactive(reactive, member)
  let [value, error] = [state.raw.value, state.error ? state.error[0] : null]
  let isSchemable = Helper.isSchemable({ reactive, member })
  let isCondition = isSchemable && Helper.isCondition({ reactive, value })
  let title = reactive.schema.for(member).label
  let activeIndex = !isSchemable || !isCondition ? 0 : 1
  let labels = !isSchemable ? [] : [
    I18n.global.t('section.member.prop.always'),
    I18n.global.t('section.member.prop.conditional'),
  ]
  let onChange = (value) => onAction && onAction({
    ...member, action: 'update', reactive, value,
  })
  let onSwitch = (index) => onAction && onAction({
    ...member, action: 'update', reactive,
    value: Helper.getDefault({
      reactive, member, conditional: index && isSchemable,
    }),
  })
  return (
    <Tabs
      className={UI.joinClass([
        'rcfm-member-prop-editor',
        `rcfm-${Struct.string.dashCase(TypeCap)}-prop-editor`,
      ])}
      title={title} labels={labels}
      activeIndex={activeIndex} onSwitch={onSwitch}
    >
      <TabPane>
        {activeIndex === 0 && <PropEditor
          reactive={reactive} member={member}
          value={value} exception={error} onChange={onChange}
        />}
      </TabPane>
      <TabPane>
        {activeIndex === 1 && <Condition
          reactive={reactive} member={member}
          value={value} exception={error}
          onChange={onChange} ResultComponent={PropEditor}
          defaultResult={Helper.getDefault({ reactive, member })}
        />}
      </TabPane>
    </Tabs>
  )
}
