import { UI } from 'rcfm/helper'
import React from 'react'



export default function SingleSelect(props = {}) {
  const Helper = UI.Component.Form.SingleSelect
  const UseNonblankName = UI.React.hooks.useNonblankName
  let {
    id, name, value, placeholder, includeblank,
    options, disabled, readonly, onChange, customProps,
  } = Helper.getProps(props)
  name = UseNonblankName(name)
  let className = UI.joinClass(
    'rcfm-component-fmselect',
    Helper.joinModifierClass(props), props.className,
  )
  let label = Helper.findLabel({ options, value })
  return (<div id={id} className={className} >
    <select {...{
      ...customProps,
      className: 'rcfm-fmtag', name, value,
      disabled: disabled || readonly, readOnly: disabled || readonly,
      onChange(e) {
        let index = e.target.selectedIndex - 1
        let newValue = index < 0 ? null : options[index].value
        onChange && onChange(newValue)
      },
    }} >
      <option
        className="rcfm-fmtag"
        style={{ display: includeblank ? 'initial' : 'none' }}
      />
      {options.map((option) =>
        <option
          className="rcfm-fmtag"
          key={option.value} value={option.value}
        >
          {option.label}
        </option>,
      )}
    </select>
    <input {...{
      className: 'rcfm-fmtag', tabIndex: -1,
      placeholder, value: label, onChange() {},
      disabled: disabled || readonly, readOnly: disabled || readonly,
    }} />
  </div>)
}

