import { UI } from 'rcfm/helper'
import React from 'react'



function SubformFieldsetPreviewer(props = {}) {
  const Helper = UI.Section.Member
  const UseReactive = UI.React.hooks.useReactive
  const { reactive, member } = Helper.getProps(props)
  const order = UseReactive(reactive, { id: member.id, prop: 'order' }).value
  if (order !== 0) { return null }
  return (<div className="rcfm-subform-fieldset-previewer">
    {props.children}
  </div>)
}

SubformFieldsetPreviewer.abilities = { WRAP_DESCENDANTS: true }



export default SubformFieldsetPreviewer
