import { UI } from 'rcfm/helper'
import React from 'react'
import FieldRenderer from './helpers/renderer.jsx'



function Component(properties = {}) {
  const TextInput = UI.use('React.Component.Form.TextInput')
  return (<TextInput {...{ ...properties, multiline: true }}/>)
}



export default function Renderer(props = {}) {
  return (<FieldRenderer Component={Component} props={props}/>)
}
