import { UI } from 'rcfm/helper'
import React from 'react'



export default function Item(props = {}) {
  const Button = UI.use('React.Component.Button')
  const ButtonGroup = UI.use('React.Component.ButtonGroup')
  const Name = UI.use('React.Section.Member.PropEditor.Validation.ItemName')
  const Rule = UI.use('React.Section.Member.PropEditor.Validation.ItemRule')
  let { reactive, member, value, onChange, actions, scope } = props
  return (<div className="rcfm-member-prop-editor-validation-item">
    <Name
      reactive={reactive} member={member}
      value={value} onChange={onChange}
      scope={scope}
    />
    {value.name && <Rule
      reactive={reactive} member={member}
      value={value} onChange={onChange}
      scope={scope}
    />}
    {(actions.includes('delete') || actions.includes('append')) && <ButtonGroup>
      {actions.includes('delete') && <Button
        color="danger" icon="cross"
        onClick={() => onChange({ action: 'delete' })}
      />}
      {actions.includes('append') && <Button
        color="secondary" icon="plus"
        onClick={() => onChange({ action: 'append' })}
      />}
    </ButtonGroup>}
  </div>)
}
