import { Struct } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



export default function Editor(props = {}) {
  const Helper = UI.Section.Member.Prop
  const UseReactive = UI.React.hooks.useReactive
  let { reactive, member, onAction } = Helper.getProps(props)
  let state = UseReactive(reactive, { id: member.id, prop: 'type' })
  if (!state) { return null }
  let TypeCap = Struct.string.capitalize(state.value)
  const PropEditor = UI.try([
    `React.Section.${TypeCap}.PropEditor`,
    'React.Section.Member.PropEditor.Fallback',
  ])
  return (<PropEditor {...{
    ...props, onAction,
    key: `${member.id}/${member.prop}` }}
  />)
}
