import { UI } from 'rcfm/helper'
import React, { useState, useEffect, useRef } from 'react'



export default function Builder(props = {}) {
  const Helper = UI.Section.Member
  const UseReactive = UI.React.hooks.useReactive
  const Alert = UI.use('React.Component.Alert')
  const AlertText = UI.use('React.Component.AlertText')
  const MemberEditor = UI.use('React.Section.Member.Editor')
  const MemberTree = UI.use('React.View.Form.Builder.Tree')
  const MemberInserter = UI.use('React.View.Form.Builder.Inserter')
  let reactive = UI.getProp(props, 'reactive', { required: true })
  let rootId = reactive.family.root().id
  let state = UseReactive(reactive, { id: rootId, prop: 'children' })
  let children = state ? state.value : []
  let editorRef = useRef()
  let [editId, setEditId] = useState('')
  let [exception, setException] = useState(null)
  let [exceptionTimer, setExceptionTimer] = useState(null)
  useEffect(() => () => clearTimeout(exceptionTimer), [exceptionTimer])
  let onAction = (action = {}) => {
    try {
      let [act, id] = [action.action, action.id]
      if (act === 'edit') { return setEditId(id) }
      let result = Helper.onAction({ ...action, reactive })
      if (act !== 'delete') { return result }
      if (reactive.family.hasnt({ id: editId })) { setEditId('') }
      return result
    } catch (error) {
      setException(error)
      setExceptionTimer(setTimeout(() => setException(null), 3000))
    }
  }
  return (<div className="rcfm-form-builder">
    <div className="rcfm-form-builder-previewer">
      {children.length ? children.map((chd) =>
        <MemberTree
          key={chd.id}
          reactive={reactive}
          member={chd}
          onAction={onAction}
        />,
      ) : <div className="rcfm-form-builder-inserter-of-root">
        <MemberInserter
          reactive={reactive}
          member={{ id: rootId }}
          onAction={onAction}
        />
      </div>}
      {exception && <Alert className="rcfm-form-builder-exception" type="error">
        <AlertText>{exception.message}</AlertText>
      </Alert>}
    </div>
    {editId && <div
      ref={editorRef}
      className="rcfm-form-builder-editor"
      onClick={(e) => e.target === editorRef.current && setEditId('') }
    >
      <div className="rcfm-form-builder-editor-container">
        <MemberEditor
          reactive={reactive}
          member={{ id: editId }}
          onAction={onAction}
        />
      </div>
    </div>}
  </div>)
}
