import { UI } from 'rcfm/helper'
import React from 'react'



export default function Checkboxes(props = {}) {
  const Helper = UI.Component.Form.Checkboxes
  const UseNonblankName = UI.React.hooks.useNonblankName
  let {
    id, name, value, options, disabled,
    readonly, onChange, customProps,
  } = Helper.getProps(props)
  name = UseNonblankName(name)
  let className = UI.joinClass(
    'rcfm-component-fmcheck',
    Helper.joinModifierClass(props), props.className,
  )
  return (<div id={id} className={className} >
    {options.map((option) =>
      <label className="rcfm-fmtag" key={option.value} >
        <input {...{
          ...customProps,
          className: 'rcfm-fmtag', type: 'checkbox', name,
          value: option.value, checked: value.includes(option.value),
          disabled: disabled || readonly, readOnly: disabled || readonly,
          onChange(e) { onChange && onChange(Helper.mutateValue({
            value, [e.target.checked ? 'insert' : 'remove']: option.value,
          })) },
        }} />
        {option.label}
      </label>,
    )}
  </div>)
}
