import { Struct } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



function Renderer(props = {}) {
  const UseReactive = UI.React.hooks.useReactive
  let { reactive, member, onAction } = UI.Section.Member.getProps(props)
  let state = UseReactive(reactive, { id: member.id, prop: 'type' })
  if (!state) { return null }
  const Member = getMemberRenderer(state.value)
  return (<Member {...{ ...props, onAction, key: member.id }} />)
}

Renderer.abilities = function(props = {}) {
  let { reactive, member } = UI.Section.Member.getProps(props)
  let type = reactive.get({ id: member.id, prop: 'type' })
  if (!type) { return false }
  const Member = getMemberRenderer(type)
  return Member.abilities || {}
}

function getMemberRenderer(memberType = '') {
  const MemberType = Struct.string.capitalize(memberType)
  return UI.use(`React.Section.${MemberType}`)
}



export default Renderer
