import { Struct } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



export default function ItemRule(props = {}) {
  const Helper = UI.Section.Member.Validation
  let { reactive, member, value, onChange /* scope */ } = props
  let RuleType = Helper.getRuleType({ reactive, member, value })
  if (!RuleType) { return null }
  let NameCap = Struct.string.capitalize(value.name)
  const [Fallback, Custom] = [
    `React.Component.Form.${RuleType}`,
    `React.Section.Member.PropEditor.Validation.ItemRule.${NameCap}`,
  ]
  const RuleEditor = UI.try([Custom, Fallback])
  return (
    <RuleEditor
      value={value.rule}
      onChange={(rule) => onChange({ action: 'update', rule })}
      reactive={UI.has(Custom) ? reactive : null}
      member={UI.has(Custom) ? member : null}
    />
  )
}
