import { UI } from 'rcfm/helper'
import React from 'react'



export default function Editor(props = {}) {
  const Helper = UI.Section.Member.Options
  const Highlight = UI.use('React.Component.Highlight')
  const Option = UI.use('React.Section.Member.PropEditor.Options.Item')
  let { reactive, member, exception, value, onChange } = Helper.getProps(props)
  onChange = Helper.decorateOnChange({ reactive, member, value, onChange })
  let [hint, data] = [exception?.message || '', exception?.data || {}]
  return (<div className="rcfm-member-prop-editor-options">
    <Highlight type="error"
      hint={hint}
      on={exception && !('optionIndex' in data) ? true : false}
    >
      {value.map((option, index) =>
        <Option
          key={index}
          reactive={reactive} member={member} value={option}
          onChange={(mu) => onChange({ ...mu, index })}
          exception={exception && data.optionIndex === index ? exception : null}
        />,
      )}
    </Highlight>
  </div>)
}
