import { UI } from 'rcfm/helper'
import React from 'react'



export default function MultipleSelect(props = {}) {
  const Helper = UI.Component.Form.MultipleSelect
  const UseNonblankName = UI.React.hooks.useNonblankName
  let {
    id, name, value, options, disabled,
    readonly, onChange, customProps,
  } = Helper.getProps(props)
  name = UseNonblankName(name)
  let className = UI.joinClass(
    'rcfm-component-fmselect',
    Helper.joinModifierClass(props), props.className,
  )
  return (<div id={id} className={className} >
    <select {...{
      ...customProps,
      className: 'rcfm-fmtag', multiple: true, name, value,
      disabled: disabled || readonly, readOnly: disabled || readonly,
      onChange(e) {
        let newValue = []
        Array.from(e.target.options).forEach((option, index) => {
          option.selected && newValue.push(options[index].value)
        })
        onChange && onChange(newValue)
      },
    }} >
      {options.map((option) =>
        <option
          className="rcfm-fmtag"
          key={option.value} value={option.value} >
          {option.label}
        </option>,
      )}
    </select>
  </div>)
}
