import { I18n } from 'rcfm'
import { UI } from 'rcfm/helper'
import React, { useState, useEffect } from 'react'



export default function WhenBranchRuleProvider(props = {}) {
  const GetProviders = UI.Section.Member.Condition.getProviders
  const JoinProvider = UI.Section.Member.Condition.joinProvider
  const SplitProvider = UI.Section.Member.Condition.splitProvider
  const SingleSelect = UI.use('React.Component.Form.SingleSelect')
  let { reactive, member, value, onChange } = props
  let reference = value.expression.value
  let { id, prop } = reactive.schema.reference.parse(
    reference,
    { relier: { id: member.id, prop: member.prop } },
  ) || {}
  let [label, setLabel] = useState(reactive.get({ id, prop: 'label' }))
  let [providers, setProviders] = useState(GetProviders({ reactive, member }))
  useEffect(() => !id ? undefined : reactive.subscribe({ id }, () => {
    let newLabel = reactive.get({ id, prop: 'label' })
    if (newLabel !== label) { return }
    setLabel(newLabel)
    setProviders(GetProviders({ reactive, member }))
  }), [reactive, member, id, label, setLabel, setProviders, GetProviders])
  return (
    <SingleSelect className="rcfm-member-prop-editor-condition-rule-provider"
      options={providers}
      value={JoinProvider({ id, prop })}
      placeholder={
        providers.length ? null : I18n.global.t('section.member.noProviders')
      }
      onChange={(value) => onChange({
        target: 'rule', action: 'update',
        value: reactive.schema.reference.stringify(
          SplitProvider(value), {
            format: 'interpolation',
            relier: { id: member.id, prop: member.prop },
          },
        ),
      })}
      onFocus={() => setProviders(GetProviders({ reactive, member }))}
    />
  )
}
