import { Struct } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



function MemberTree(props = {}) {
  const Helper = UI.Section.Member
  const UseReactive = UI.React.hooks.useReactive
  const MemberPreviewer = UI.use('React.Section.Member.Previewer')
  const MemberActions = UI.use('React.View.Form.Builder.Actions')
  const MemberInserter = UI.use('React.View.Form.Builder.Inserter')
  let { reactive, member, onAction } = Helper.getProps(props)
  let childre = UseReactive(reactive, { id: member.id, prop: 'children' }).value
  let parentId = reactive.family.ancestor(member).id
  let type = reactive.get({ id: member.id, prop: 'type' })
  let typeDashed = Struct.string.dashCase(type)
  let abilities = MemberPreviewer.abilities({ reactive, member })
  return (<>
    <MemberWrapper
      typeDashed={typeDashed}
      _withoutWrapper={props._withoutWrapper}
    >
      <MemberActions
        reactive={reactive}
        member={member}
        onAction={onAction}
      />
      <MemberPreviewer
        reactive={reactive}
        member={member}
        onAction={onAction}
      >
        {abilities.WRAP_DESCENDANTS && (
          childre.length > 0 ? <Descendants
            reactive={reactive}
            onAction={onAction}
            childre={childre}
            _withoutWrapper={true}
          /> : <MemberInserter
            reactive={reactive}
            member={member}
            onAction={onAction}
          />
        )}
      </MemberPreviewer>
    </MemberWrapper>
    {!abilities.RENDER_DESCENDANTS &&
     !abilities.WRAP_DESCENDANTS && (
      childre.length > 0 ? <div className={UI.joinClass({
        root: 'rcfm-form-builder',
        klass: ['descendants', `${typeDashed}-descendants`],
      })} >
        <Descendants
          reactive={reactive}
          onAction={onAction}
          childre={childre}
        />
      </div> : <MemberInserter
        reactive={reactive}
        member={member}
        onAction={onAction}
      />
    )}
    <MemberInserter
      reactive={reactive}
      member={{ id: parentId }}
      onAction={(action = {}) => onAction({
        ...action, props: {
          ...action.props,
          order: reactive.get({ id: member.id, prop: 'order' }) + 1,
        },
      })}
    />
  </>)
}

function MemberWrapper(props = {}) {
  const { _withoutWrapper, typeDashed } = props
  if (_withoutWrapper) { return props.children }
  return <div className={UI.joinClass({
    root: 'rcfm-form-builder',
    klass: ['member', typeDashed],
  })} >
    {props.children}
  </div>
}

function Descendants(props = {}) {
  const { reactive, onAction, childre, _withoutWrapper } = props
  return childre.map((chd) =>
    <MemberTree
      key={chd.id}
      reactive={reactive}
      member={chd}
      onAction={onAction}
      _withoutWrapper={_withoutWrapper}
    />,
  )
}



export default MemberTree
