import  { Struct } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



function Previewer(props = {}) {
  const Helper = UI.Section.Member
  const UseReactive = UI.React.hooks.useReactive
  let { reactive, member, onAction } = Helper.getProps(props)
  let state = UseReactive(reactive, { id: member.id, prop: 'type' })
  if (!state) { return null }
  const Previewer = UI.try([
    getPreviewerName(state.value),
    'React.Section.Member.Previewer.Fallback',
  ])
  return (<Previewer {...{ ...props, onAction, key: member.id }} />)
}

Previewer.abilities = function(props = {}) {
  const Helper = UI.Section.Member
  let { reactive, member } = Helper.getProps(props)
  let type = reactive.get({ id: member.id, prop: 'type' })
  if (!type) { return false }
  const PreviewerName = getPreviewerName(type)
  if (!UI.has(PreviewerName)) { return {} }
  return UI.use(PreviewerName).abilities || {}
}

function getPreviewerName(memberType = '') {
  const MemberType = Struct.string.capitalize(memberType)
  return `React.Section.${MemberType}.Previewer`
}



export default Previewer
