import { UI } from 'rcfm/helper'
import React from 'react'



export default function Tabs(props = {}) {
  const Helper = UI.Component.Tabs
  let {
    title, labels, activeIndex,
    onSwitch, customProps,
  } = Helper.getProps(props)
  let className = UI.joinClass('rcfm-component-tabs', props.className)
  return (<div {...{ ...customProps, className }} >
    <div className="rcfm-component-tabs-header" >
      {title && <h6 className="rcfm-component-tabs-title">{title}</h6>}
      {labels.map((label, index) =>
        <label
          key={label} tabIndex={0}
          onClick={() => onSwitch && onSwitch(index) }
          onKeyDown={(e) => e.keyCode === 13 && onSwitch && onSwitch(index) }
          className={UI.joinClass({
            root: 'rcfm-component-tabs',
            klass: ['label', activeIndex === index ? 'label-active' : ''],
          })}
        >
          {label}
        </label>,
      )}
    </div>
    {!labels.length ?
      <div
        className={UI.joinClass({
          root: 'rcfm-component-tabs',
          klass: ['pane', 'pane-active'],
        })}
      >
        {props.children}
      </div> :
      labels.map((label, index) =>
        <div
          key={label}
          className={UI.joinClass({
            root: 'rcfm-component-tabs',
            klass: ['pane', activeIndex === index ? 'pane-active' : ''],
          })}
        >
          {props.children[index]}
        </div>,
      )}
  </div>)
}
