import { Struct } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



export default function Previewer(props = {}) {
  const Helper = UI.Section.Member
  const UseReactive = UI.React.hooks.useReactive
  const Highlight = UI.use('React.Component.Highlight')
  const Renderer = UI.use('React.Section.Member')
  let { reactive, member, onAction } = Helper.getProps(props)
  let state = UseReactive(reactive, { id: member.id })
  let MemberType = reactive.get({ id: member.id, prop: 'type' })
  return (
    <div className={UI.joinClass([
      'rcfm-member-previewer',
      `rcfm-${Struct.string.dashCase(MemberType)}-previewer`,
    ])} >
      <Highlight
        type="error"
        on={state?.errors ? true : false}
        hint={Helper.errorMessages({ reactive, member, errors: state.errors })}
      >
        <fieldset
          disabled
          className="rcfm-member-previewer-container"
        >
          <Renderer
            reactive={reactive}
            member={member}
            onAction={onAction}
          />
        </fieldset>
      </Highlight>
    </div>
  )
}
