import { UI } from 'rcfm/helper'
import React from 'react'



export default function ItemName(props = {}) {
  const Helper = UI.Section.Member.Validation
  const SingleSelect = UI.use('React.Component.Form.SingleSelect')
  let { reactive, member, value, onChange, scope } = props
  let validators = Helper.getValidators({ reactive, member, scope })
  return (
    <SingleSelect
      value={value.name} options={validators}
      onChange={(name) => onChange({ action: 'update', name })}
    />
  )
}
