import { I18n } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



function SubformFieldset(props = {}) {
  const Helper = UI.Section.Subform
  const UseReactive = UI.React.hooks.useReactive
  const Button = UI.use('React.Component.Button')
  let { reactive, member, onAction } = UI.Section.Member.getProps(props)
  let parentId = reactive.family.ancestor({ id: member.id }).id
  let order = UseReactive(reactive, { id: member.id, prop: 'order' }).value
  return (<div className="rcfm-subform-fieldset">
    <div className="rcfm-subform-fieldset-descendants">
      {props.children}
    </div>
    <div className="rcfm-subform-fieldset-actions">
      <Button color="secondary" size="small"
        onClick={() => {
          if (!onAction) { return }
          let [family, id] = [reactive.family, member.id]
          family.sibling({ id }) ?
            onAction({ action: 'delete', id }) :
            Helper.collectFields({
              reactive, collection: family.descendant({ id }),
            }).forEach((chd) => onAction({
              action: 'update', id: chd.id, prop: 'value', value: null,
            }))
        }}
      >
        {I18n.global.t('section.subform.fieldset.remove')}
      </Button>
      <Button color="secondary" size="small"
        onClick={() => onAction && onAction({
          action: 'add', parentId,
          props: { type: 'SubformFieldset', order: order + 1 },
        })}
      >
        {I18n.global.t('section.subform.fieldset.insert')}
      </Button>
    </div>
  </div>)
}

SubformFieldset.abilities = { WRAP_DESCENDANTS: true }



export default SubformFieldset
