import { UI } from 'rcfm/helper'
import React from 'react'



function Subform(props = {}) {
  const Helper = UI.Section.Member
  const UseReactive = UI.React.hooks.useReactive
  const Field = UI.use('React.Component.Form.Field')
  const Highlight = UI.use('React.Component.Highlight')
  let { reactive, member } = Helper.getProps(props)
  let state = UseReactive(reactive, { id: member.id })
  let { label, editable } = state.values
  return (
    <Field className="rcfm-subform" label={label}>
      <Highlight
        type="error"
        hintPosition="top"
        className="rcfm-subform-container"
        on={state?.errors ? true : false}
        hint={Helper.errorMessages({ reactive, member, errors: state.errors })}
      >
        <fieldset
          className="rcfm-subform-descendants"
          disabled={!editable}
        >
          {props.children}
        </fieldset>
      </Highlight>
    </Field>
  )
}

Subform.abilities = { WRAP_DESCENDANTS: true }



export default Subform
