import { Type } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



export default function Button(props = {}) {
  const Helper = UI.Component.Button
  let {
    customProps,
    type, disabled, onClick,
    icon, topIcon, leftIcon, rightIcon,
  } = Helper.getProps(props)
  let className = Helper.joinWrapperClass(props)
  let Content = (() => {
    if (icon) { return IconOnly }
    if (topIcon || leftIcon || rightIcon) { return ChildrenWithIcon }
    return ChildrenOnly
  })()
  return (
    <button {...{ ...customProps, type, disabled, onClick, className }} >
      <Content icon={icon || topIcon || leftIcon || rightIcon} >
        {props.children}
      </Content>
    </button>
  )
}




function ChildrenOnly(props = {}) {
  return (<>{props.children}</>)
}

function IconOnly(props = {}) {
  const Icon = UI.use('React.Component.Icon')
  if (Type.is.boolean(props.icon)) { return (<>{props.children}</>) }
  return (<Icon name={props.icon} />)
}

function ChildrenWithIcon(props = {}) {
  return (<>
    <IconOnly icon={props.icon} />
    <ChildrenOnly>{props.children}</ChildrenOnly>
  </>)
}
