import { UI } from 'rcfm/helper'
import React from 'react'



export default function ButtonGroup(props = {}) {
  const Helper = UI.Component.Button.Group
  let { customProps } = Helper.getProps(props)
  let className = Helper.joinWrapperClass(props)
  return (
    <div {...{ ...customProps, className }} >
      {props.children}
    </div>
  )
}
