import { UI } from 'rcfm/helper'
import React from 'react'



export default function Editor(prop = {}) {
  const { getProps, decorateOnChange } = UI.Section.Member.Validation
  const Highlight = UI.use('React.Component.Highlight')
  const Item = UI.use('React.Section.Member.PropEditor.Validation.Item')
  let { reactive, member, value, exception, actions, onChange } = getProps(prop)
  onChange = decorateOnChange({ reactive, value, onChange })
  return (<div className="rcfm-member-prop-editor-validation">
    {value.map((item, index) =>
      <Highlight type="error"
        key={index} hint={exception?.message}
        on={exception?.data?.validationIndex === index}
      >
        <Item
          scope="validation"
          reactive={reactive} member={member}
          value={item} actions={actions}
          onChange={(mu) => onChange({ ...mu, index })}
        />
      </Highlight>,
    )}
  </div>)
}
