import { I18n } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



export default function WhenBranch(props = {}) {
  const Helper = UI.Section.Member.Condition
  const [Button, ButtonGroup, Highlight, BranchRule] = [
    UI.use('React.Component.Button'),
    UI.use('React.Component.ButtonGroup'),
    UI.use('React.Component.Highlight'),
    UI.use('React.Section.Member.PropEditor.Condition.WhenBranch.Rule'),
  ]
  let { reactive, member, value, onChange, ResultComponent, exception } = props
  let [hint, data] = [exception?.message || '', exception?.data || {}]
  return (<div className="rcfm-member-prop-editor-condition-when-branch">
    <h6 className="rcfm-member-prop-editor-condition-branch-title">
      {I18n.global.t('section.member.prop.condition.whenBranch.title')}
    </h6>
    <Highlight
      type="error"
      hint={hint}
      on={Helper.isRulesException(exception)}
    >
      {value.rules.map((rule, ruleIndex) =>
        <BranchRule
          key={ruleIndex}
          reactive={reactive} member={member}
          value={rule} onChange={(mu) => onChange({ ...mu, ruleIndex })}
          exception={data.ruleIndex === ruleIndex ? exception : null}
        />,
      )}
    </Highlight>
    <h6 className="rcfm-member-prop-editor-condition-branch-title">
      {I18n.global.t('section.member.prop.condition.whenBranch.resultTitle')}
    </h6>
    <ResultComponent
      reactive={reactive} member={member}
      value={value.result} onChange={(result) => onChange({
        target: 'when', action: 'update', result,
      })}
      exception={!Helper.isConditionException(exception) ? exception : null}
    />
    <ButtonGroup fluid>
      <Button
        color="danger"
        onClick={() => onChange({ target: 'when', action: 'delete' })}
      >
        {I18n.global.t('section.member.prop.condition.whenBranch.delete')}
      </Button>
      <Button
        color="secondary"
        onClick={() => onChange({ target: 'when', action: 'append' })}
      >
        {I18n.global.t('section.member.prop.condition.whenBranch.add')}
      </Button>
    </ButtonGroup>
  </div>)
}

