import { UI } from 'rcfm/helper'
import React from 'react'



export default function NumberInput(props = {}) {
  const Helper = UI.Component.Form.NumberInput
  const UseNonblankName = UI.React.hooks.useNonblankName
  let {
    id, name, value, placeholder,
    disabled, readonly, onChange, customProps,
  } = Helper.getProps(props)
  name = UseNonblankName(name)
  let className = UI.joinClass(
    'rcfm-component-fminput',
    Helper.joinModifierClass(props), props.className,
  )
  return (
    <div id={id} className={className}>
      <input {...{
        ...customProps,
        className: 'rcfm-fmtag', type: 'number', name,
        placeholder, value: isNaN(value) ? '' : value,
        disabled: readonly || disabled, readOnly: readonly || disabled,
        onChange(e) {
          if (!onChange) { return }
          onChange(e.target.value.length ? Number(e.target.value) : NaN)
        },
      }} />
    </div>
  )
}
