import { UI } from 'rcfm/helper'
import React from 'react'



export default function Field(props = {}) {
  const Helper = UI.Component.Form.Field
  let { customProps, label, hint, error } = Helper.getProps(props)
  return (
    <div {...{
      ...customProps,
      className: Helper.joinWrapperClass(props),
    }} >
      {label &&
        <label className={Helper.joinLabelClass(props)} >
          {label}
        </label>
      }
      {props.children}
      {!error.length ? null :
        <p className={Helper.joinErrorClass(props)} >
          {error.map((e) => e + ' ')}
        </p>
      }
      {!error.length && hint &&
        <p className={Helper.joinHintClass(props)} >
          {hint}
        </p>
      }
    </div>
  )
}
