import { UI } from 'rcfm/helper'
import React from 'react'



function SubformPreviewer(props = {}) {
  const Helper = UI.Section.Member
  const UseReactive = UI.React.hooks.useReactive
  const Field = UI.use('React.Component.Form.Field')
  const Highlight = UI.use('React.Component.Highlight')
  let { reactive, member } = Helper.getProps(props)
  let state = UseReactive(reactive, { id: member.id })
  let disabled = !state.values.visible || !state.values.editable
  return (
    <div className="rcfm-subform-previewer">
      <Highlight
        type="error"
        hintPosition="top"
        on={state?.errors ? true : false}
        data-disabled={disabled ? true : null}
        className="rcfm-subform-previewer-container"
        hint={Helper.errorMessages({ reactive, member, errors: state.errors })}
      >
        <Field label={state.values.label}>
          {props.children}
        </Field>
      </Highlight>
    </div>
  )
}

SubformPreviewer.abilities = { WRAP_DESCENDANTS: true }



export default SubformPreviewer
