import { Struct } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



export default function Editor(props = {}) {
  const Helper = UI.Section.Member
  const PropEditor = UI.use('React.Section.Member.PropEditor')
  let { reactive, member, onAction } = Helper.getProps(props)
  let schema = reactive.schema.for({ id: member.id })
  let MemberType = reactive.get({ id: member.id, prop: 'type' })
  return (
    <div className={UI.joinClass([
      'rcfm-member-editor',
      `rcfm-${Struct.string.dashCase(MemberType)}-editor`,
    ])} >
      {schema.map((S) =>
        <PropEditor
          key={S.prop}
          reactive={reactive}
          onAction={onAction}
          member={{ id: member.id, prop: S.prop }}
        />,
      )}
    </div>
  )
}
