import { Struct } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



function MemberTree(props = {}) {
  const Helper = UI.Section.Member
  const UseReactive = UI.React.hooks.useReactive
  const Member = UI.use('React.Section.Member')
  let { reactive, member, onAction } = Helper.getProps(props)
  let visible = UseReactive(reactive, { id: member.id, prop: 'visible' }).value
  let childre = UseReactive(reactive, { id: member.id, prop: 'children' }).value
  if (visible === false) { return null }
  let type = reactive.get({ id: member.id, prop: 'type' })
  let typeDashed = Struct.string.dashCase(type)
  let abilities = Member.abilities({ reactive, member })
  return (<>
    <MemberWrapper
      typeDashed={typeDashed}
      _withoutWrapper={props._withoutWrapper}
    >
      <Member
        reactive={reactive}
        member={member}
        onAction={onAction}
      >
        {childre.length > 0 &&
         abilities.WRAP_DESCENDANTS &&
        <Descendants
          reactive={reactive}
          onAction={onAction}
          childre={childre}
          _withoutWrapper={true}
        />}
      </Member>
    </MemberWrapper>
    {childre.length > 0 &&
     !abilities.RENDER_DESCENDANTS &&
     !abilities.WRAP_DESCENDANTS &&
    <div className={UI.joinClass({
      root: 'rcfm-form',
      klass: ['descendants', `${typeDashed}-descendants`],
    })} >
      <Descendants
        reactive={reactive}
        onAction={onAction}
        childre={childre}
      />
    </div>}
  </>)
}

function MemberWrapper(props = {}) {
  const { _withoutWrapper, typeDashed } = props
  if (_withoutWrapper) { return props.children }
  return <div className={UI.joinClass({
    root: 'rcfm-form',
    klass: ['member', typeDashed],
  })} >
    {props.children}
  </div>
}

function Descendants(props = {}) {
  const { reactive, onAction, childre, _withoutWrapper } = props
  return childre.map((chd) =>
    <MemberTree
      key={chd.id}
      reactive={reactive}
      member={chd}
      onAction={onAction}
      _withoutWrapper={_withoutWrapper}
    />,
  )
}



export default MemberTree
