import { UI } from 'rcfm/helper'
import React from 'react'



export default function Icon(props = {}) {
  let { name, customProps } = UI.getProps(props, {
    name: { dataType: 'String', required: true },
  })
  let className = UI.joinClass({
    root: 'rcfm-component-ico',
    klass: name,
  }, props.className)
  return (<i {...{ ...customProps, className }}></i>)
}
