import { UI } from 'rcfm/helper'
import React, { useRef } from 'react'



export default function Select(props = {}){
  let ref = useRef()
  let { options, onSelect, onCancel } = props
  const UseOnClickOutside = UI.React.hooks.useOnClickOutside
  const Button = UI.use('React.Component.Button')
  const ButtonGroup = UI.use('React.Component.ButtonGroup')
  UseOnClickOutside(ref, onCancel)
  return (<div className="rcfm-form-builder-inserter-child-select">
    <div
      ref={ref}
      className="rcfm-form-builder-inserter-child-select-container"
    >
      <ButtonGroup vertical fluid>
        {options.map((option) =>
          <Button
            key={option.type}
            color="secondary"
            onClick={() => onSelect(option.type)}
          >
            {option.label}
          </Button>,
        )}
      </ButtonGroup>
    </div>
  </div>)
}
