import { UI } from 'rcfm/helper'
import React from 'react'



export default function Editor(prop = {}) {
  const Helper = UI.Section.Member.Condition
  const Highlight = UI.use('React.Component.Highlight')
  const When = UI.use('React.Section.Member.PropEditor.Condition.WhenBranch')
  const Else = UI.use('React.Section.Member.PropEditor.Condition.ElseBranch')
  let {
    reactive, member, exception, value,
    ResultComponent, defaultResult, onChange,
  } = Helper.getProps(prop)
  onChange = Helper.decorateOnChange({
    reactive, value, defaultResult, onChange,
  })
  let [hint, data] = [exception?.message || '', exception?.data || {}]
  return (<div className="rcfm-member-prop-editor-condition">
    <Highlight
      type="error"
      hint={hint}
      on={Helper.isWhensException(exception)}
    >
      {value.whens.map((branch, branchIndex) =>
        <When
          key={branchIndex}
          reactive={reactive} member={member}
          value={branch} onChange={(mu) => onChange({ ...mu, branchIndex })}
          ResultComponent={ResultComponent}
          exception={data.branchIndex === branchIndex ? exception : null}
        />,
      )}
    </Highlight>
    <Else
      reactive={reactive} member={member}
      value={value.else} onChange={onChange}
      ResultComponent={ResultComponent}
      exception={data.branchIndex === value.whens.length ? exception : null}
    />
  </div>)
}
