import { UI } from 'rcfm/helper'
import React from 'react'



export default function WhenBranchRule(props = {}) {
  const Helper = UI.Section.Member.Condition
  const [Button, ButtonGroup, Highlight, ValidationItem, Logic, Provider] = [
    UI.use('React.Component.Button'),
    UI.use('React.Component.ButtonGroup'),
    UI.use('React.Component.Highlight'),
    UI.use('React.Section.Member.PropEditor.Validation.Item'),
    UI.use('React.Section.Member.PropEditor.Condition.WhenBranch.RuleLogic'),
    UI.use('React.Section.Member.PropEditor.Condition.WhenBranch.RuleProvider'),
  ]
  let { reactive, member, value, onChange, exception } = props
  let provider = reactive.schema.reference.parse(
    value.expression.value,
    { relier: { id: member.id, prop: member.prop } },
  )
  return (<div className="rcfm-member-prop-editor-condition-rule">
    <Highlight
      type="error"
      hint={exception?.message || ''}
      on={
        Helper.isLogicException(exception) ||
        Helper.isProviderException(exception)
      }
    >
      <div className="rcfm-member-prop-editor-condition-rule-row" >
        {value.logic && <Logic
          reactive={reactive} member={member}
          value={value} onChange={onChange}
        />}
        <Provider
          reactive={reactive} member={member}
          value={value} onChange={onChange}
        />
        <ButtonGroup>
          <Button
            color="danger" icon="cross"
            onClick={() => onChange({ target: 'rule', action: 'delete' })}
          />
          <Button
            color="secondary" icon="plus"
            onClick={() => onChange({ target: 'rule', action: 'append' })}
          />
        </ButtonGroup>
      </div>
    </Highlight>
    {value.expression.value && <Highlight
      type="error"
      hint={exception?.message || ''}
      on={Helper.isValidationException(exception)}
    >
      <div className="rcfm-member-prop-editor-condition-rule-row" >
        <ValidationItem
          scope="condition"
          reactive={reactive} member={provider}
          value={value.expression} actions={['update']}
          onChange={(mu) => onChange({
            ...mu, target: 'rule', action: 'update',
          }) }
        />
      </div>
    </Highlight>}
  </div>)
}
