import { useState, useEffect, useRef } from 'react'



function useIsMounted() {
  let isMounted = useRef(false)
  useEffect(() => {
    isMounted.current = true
    return () => isMounted.current = false
  }, [isMounted])
  return isMounted
}

function useReactive(reactive, member = {}) {
  member || (member = {})
  let { id, prop } = member
  let isMounted = useIsMounted()
  let [value, setValue] = useState(getProp(reactive, member))
  useEffect(() => {
    if (!id) { return }
    return reactive.subscribe({ id }, () => {
      if (!isMounted.current) { return }
      if (reactive.family.hasnt({ id })) { return }
      setValue(getProp(reactive, prop ? { id, prop } : { id }))
    })
  }, [reactive, id, prop, isMounted])
  return value
}

function getProp(reactive, member){
  return reactive.get(member, { include: ['raw', 'error'] })
}



export default useReactive
