import { UI } from 'rcfm/helper'
import React from 'react'



export default function ObjectEditor(props = {}) {
  const Helper = UI.Component.Form.Object
  const TextInput = UI.use('React.Component.Form.TextInput')
  return (
    <TextInput {...{
      ...props,
      className: UI.joinClass('rcfm-component-fmobject', props.className),
      value: Helper.decorateValue(props.value),
      onChange: Helper.decorateOnChange(props.onChange),
    }} />
  )
}

